body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#app .v-header {
  background: #294e80;
  padding: 0;
  height: 100px; }
  #app .v-header .trigger {
    font-size: 18px; }
  #app .v-header .header-right {
    float: right;
    color: #fff;
    font-size: 16px; }
    #app .v-header .header-right span {
      display: inline-block;
      margin: 0 20px; }
    #app .v-header .header-right .exit {
      margin-right: 20px;
      font-size: 18px; }
      #app .v-header .header-right .exit:hover {
        color: #e39458; }

#Sider h2 {
  color: #fff;
  font-size: 18px;
  height: 90px;
  line-height: 80px;
  text-align: center;
  font-weight: 800; }

#user .information {
  padding: 10px; }
  #user .information .addButton {
    margin-left: 10px; }
  #user .information .search {
    float: right; }

.item {
  padding: 10px 0; }
  .item input {
    width: 80%; }
  .item span {
    margin: 0 10px; }

.AddProduct {
  height: 40px;
  line-height: 40px; }
  .AddProduct .icon {
    font-size: 16px;
    margin-right: 5px; }
  .AddProduct span {
    font-size: 16px; }
  .AddProduct button {
    margin: 4px;
    float: right; }

.twoLine {
  padding: 20px 0; }

.cardItem {
  box-shadow: 5px 5px 5px #bbb;
  margin-bottom: 50px; }
  .cardItem .toDetail:hover {
    color: red; }

.clothes .pic {
  width: 150px;
  height: 150px; }

#order .desc {
  height: 40px;
  background: #3975ce;
  line-height: 40px;
  padding: 0 20px;
  color: #fff; }
  #order .desc .descIcon {
    font-size: 30px; }
  #order .desc span {
    font-size: 20px;
    margin-left: 10px; }

#order .orderDetail {
  width: 50%; }
  #order .orderDetail img {
    width: 60px;
    height: auto; }

.login {
  width: 100%; }
  .login .cover {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    background-color: #b8e5f8;
    background-image: url(../../static/media/bj2.f9799d88.jpg); }
  .login .card {
    width: 602px;
    height: 470px;
    border-radius: 50px;
    margin: 0 auto;
    position: absolute;
    background-color: #fff;
    opacity: 0.8;
    left: 35%;
    top: 200px; }
    .login .card .title {
      width: 100%;
      height: 120px;
      padding-top: 20px;
      text-align: center; }
      .login .card .title div {
        height: 50px;
        line-height: 50px;
        display: block; }
        .login .card .title div:nth-of-type(1) {
          font-size: 30px;
          font-weight: 700; }
        .login .card .title div:nth-of-type(2) {
          font-size: 20px;
          font-weight: 400; }
    .login .card .table {
      padding: 0 30px; }
      .login .card .table .inputItem {
        height: 60px;
        display: -webkit-flex;
        display: flex;
        border-bottom: 1px solid #d9d9d9; }
        .login .card .table .inputItem .select {
          -webkit-flex: 2 1;
                  flex: 2 1;
          margin: 10px;
          border: none; }
          .login .card .table .inputItem .select .ant-select-selection {
            border: none; }
        .login .card .table .inputItem input {
          height: 59px;
          border: none; }
          .login .card .table .inputItem input:focus {
            box-shadow: none; }
      .login .card .table button {
        width: 80%;
        height: 55px;
        margin: 30px 10% 0;
        text-align: center; }
    .login .card .text {
      color: #b2b2b2; }
    .login .card .footer {
      height: 60px;
      width: 100%;
      background-color: #f6f6f6;
      position: absolute;
      bottom: 0;
      right: 0;
      border: 1px solid #ebebeb;
      line-height: 60px; }
      .login .card .footer span {
        cursor: pointer; }
        .login .card .footer span:nth-of-type(2) {
          color: #0084ff; }

